import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import IconButton from '@mui/material/IconButton';
import ClearIcon from '@mui/icons-material/Clear';
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import { LinksHeader } from '../linksHeader/LinksHeader';
import { Box } from '@mui/material';
import { LinkChildren } from '../linkChildren/LinkChildren';

export const LinksGrid = ({ columns, childrens }) => {
  // const dispatch = useDispatch();
  // const { createTask, todos, complatedTasks, isFiltering, filterTodos } = useSelector(state => state.todo)

  return (
    <>
      <div className="parent">
        <div className="parent__header links-grid">
          {
            columns.map((el) => <LinksHeader
              key={el.field}
              {...el}
            />)
          }
          <IconButton sx={{ padding: 0, width: '20px' }} aria-label="delete" size="large">
            <ClearIcon />
          </IconButton>
        </div>
        <div className="parent__body">
          <Box sx={{ margin: 0, padding: 0, width: '100%' }}>
            {
              childrens.map(el => <LinkChildren  key={el.id} {...el} />)
            }
          </Box>
        </div>
        <IconButton sx={{ padding: 0, width: '20px', position: 'absolute', bottom: '15px', left: '15px' }} color='warning'>
          <AddCircleOutlineRoundedIcon fontSize='large' />
        </IconButton>
      </div>
    </>
  );
};
