import React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import IconButton from '@mui/material/IconButton';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import AddIcon from '@mui/icons-material/Add';
import { Box, Button } from '@mui/material';


export const LinkChildren = ({ id, summary, title, links, desc, tags }) => {
  const [valueInputLinks, setValueInputLinks] = React.useState(links);
  const [valueInputTask, setValueInputTask] = React.useState(title);
  const [valueDesc, setValueDesc] = React.useState(desc);
  const [valueTags, setValueTags] = React.useState('');

  const handleChangeLinks = (event) => {
    const {
      target: { value },
    } = event;
    setValueInputLinks(value);
    // setTimeout(() => {
    //   dispatch(setChangeTaskLinks({ id, links: value }))
    // }, 400);
  };

  const handleChangeTaskName = (event) => {
    const {
      target: { value },
    } = event;
    setValueInputTask(value);
    // setTimeout(() => {
    //   dispatch(setChangeTaskName({ id, task: value }))
    // }, 400);
  };

  const handleChangeDesc = (event) => {
    const {
      target: { value },
    } = event;
    setValueDesc(value);
    // setTimeout(() => {
    //   dispatch(setChangeTaskName({ id, task: value }))
    // }, 400);
  };

  const handleChangeTags = (event) => {
    const {
      target: { value },
    } = event;
    setValueTags(value);
    // setTimeout(() => {
    //   dispatch(setChangeTaskName({ id, task: value }))
    // }, 400);
  };

  return (
    <Accordion sx={{ marginBottom: '18px !important' }}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        sx={{ backgroundColor: '#fbf0d4' }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Typography>{summary}</Typography>
          <IconButton sx={{ padding: 0, width: '20px', ml: '10px' }} size="large">
            <MoreHorizIcon />
          </IconButton>
        </Box>
      </AccordionSummary>
      <AccordionDetails>
        <Box sx={{ margin: '5px 0', padding: 0, width: '100%' }}>
          <div className="links-task">
            <div className="links-task-item bordered">
              <input type="text" name={`title${id}`} value={valueInputTask} onChange={handleChangeTaskName} />
            </div>
            <div className="links-task-item bordered">
              <input type="text" name={`links${id}`} value={valueInputLinks} onChange={handleChangeLinks} />
            </div>
            <div className="links-task-item bordered">
              <textarea name={`desc${id}`} value={valueDesc} onChange={handleChangeDesc} />
            </div>
            <div className='links-task-item bordered' style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
              <Box sx={{ display: 'flex', alignItems: 'center', width: '100%', overflowY: 'auto', flexWrap: 'wrap' }}>
                {
                  tags.map((el, i) => <Button key={i} size='small' sx={{ padding: '0', minWidth: '48px !important', fontSize: '12px' }}>{el}</Button>)
                }

                {/* <input type="text" name={`tags${id}`} value={valueTags} onChange={handleChangeTags} /> */}
              </Box>
              <IconButton sx={{ padding: 0, width: '20px', margin: '0 3px 3px 0' }} color="primary" size="small" variant="contained">
                <AddIcon />
              </IconButton>
            </div>
          </div>
        </Box>
      </AccordionDetails>
    </Accordion>
  );
};
