import React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import IconButton from '@mui/material/IconButton';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import { Box, Container } from '@mui/material';

import { LinksGrid } from '../linksGrid/LinksGrid';

export const FieldLinks = {
  TITLE: 'title',
  LINKS: 'links',
  DESC: 'description',
  TAGS: 'tags',
}

const columns = [
  { field: FieldLinks.TITLE, headerName: 'Title' },
  { field: FieldLinks.LINKS, headerName: 'Links' },
  { field: FieldLinks.DESC, headerName: 'Description' },
  { field: FieldLinks.TAGS, headerName: 'Tags' }
];


export const LinkParent = ({ id, title, childrens }) => {

  return (
    <Box sx={{ width: '100%', marginBottom: '10px !important' }}>
      <Accordion >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          sx={{ backgroundColor: '#fbd573' }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography>{title}</Typography>
            <IconButton sx={{ padding: 0, width: '20px', ml: '15px' }} size="large">
              <MoreHorizIcon />
            </IconButton>
          </Box>
        </AccordionSummary>
        <AccordionDetails sx={{ position: 'relative' }}>
          <Container>
            <LinksGrid columns={columns} childrens={childrens} />
          </Container>
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};
